import React from 'react';
import {  View, ScreenSpinner, Panel, Epic, Button, PanelHeader, Panar, Avatar, Div, HeaderButton, Cell, Snackbar, Gallery, Group, List, InfoRow, Separator} from '@vkontakte/vkui';
import './Profile.css';
import '@vkontakte/vkui/dist/vkui.css';
import Achievment from './Achievment';

// Компонент профиля
class Profile extends React.Component {
	constructor(props) {
       super(props);

       this.state = {
          fetchedUser:this.props.fetchedUser
     }
  }

	  render() {
		        return(
                <Div id="profile-container" align="center">
                  <Div id="avatar-container">
                     <Avatar src={this.state.fetchedUser.photo_200} size={150}/>
                  </Div>

                  <Div id="name-container">
                    <Cell class="text" id="name">{this.state.fetchedUser.first_name+" "+this.state.fetchedUser.last_name}</Cell>
                  </Div>

                  <Div id="score">
                      <Cell class="text" id="score-text">
                         <InfoRow>
                           Очки: 0 
                         </InfoRow>
                      </Cell>
                  </Div>
               
                  <Group title="Достижения" id="achievements-container">
                     <Achievment lvlNum="1" title="Первопроходец" />
                  </Group>
               </Div>
		        );           
	  }
}

export default Profile;